import "./largeWidget.css";

export default function LargeWidget() {
  const Button = ({ type }) => {
    return <button className={"widgetLgButton " + type}>{type}</button>;
  };
  return (
    <div className="largeWidget">
      <h3 className="largeWidgetTitle">Key Performance indicator</h3>
      <table className="largeWidgetTable">
        <tbody>
        <tr className="largeWidgetTr">
          <th className="largeWidgetTh">Metrics</th>
          <th className="largeWidgetTh">Uptime</th>
          <th className="largeWidgetTh">Percentage</th>
          <th className="largeWidgetTh">Status</th>
        </tr>
        <tr className="largeWidgetTr">
          <td className="largeWidgetUser">
            <span className="largeWidgetName">Pyhsical CPU utlization</span>
          </td>
          <td className="largeWidgetDate">23 days 6 hours</td>
          <td className="largeWidgetAmount">56 %</td>
          <td className="largeWidgetStatus">
            <Button type="Approved" />
          </td>
        </tr>
        <tr className="largeWidgetTr">
          <td className="largeWidgetUser">
            <span className="largeWidgetName">Physical memory</span>
          </td>
          <td className="largeWidgetDate">23 days 6 hours</td>
          <td className="largeWidgetAmount">43 %</td>
          <td className="largeWidgetStatus">
            <Button type="Approved" />
          </td>
        </tr>
        <tr className="largeWidgetTr">
          <td className="largeWidgetUser">
            <span className="largeWidgetName">Datastore availability</span>
          </td>
          <td className="largeWidgetDate">23 days 6 hours</td>
          <td className="largeWidgetAmount">52 %</td>
          <td className="largeWidgetStatus">
            <Button type="Approved" />
          </td>
        </tr>
        <tr className="largeWidgetTr">
          <td className="largeWidgetUser">
            <span className="largeWidgetName">Network availability</span>
          </td>
          <td className="largeWidgetDate">23 days 6 hours</td>
          <td className="largeWidgetAmount">65 %</td>
          <td className="largeWidgetStatus">
            <button type="button" class="btn btn-success">Success</button>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
  );
}
