import "./process.css";
import { DataGrid } from "@material-ui/data-grid";
import { servicesRows } from "../../dummyData";

import React from "react";

export default function ProcessList() {


  const columns = [
    { field: "id", headerName: "ID", width: 90 },
    {
      field: "name",
      headerName: "Name",
      width: 200,
    },
    { field: "pid", headerName: "PID", width: 150 },
    {
      field: "description",
      headerName: "Description",
      width: 200,
    },
    {
      field: "status",
      headerName: "Status",
      width: 160,
    },

  ];

  return (
    <div className="applicationList">
      <DataGrid
        rows={servicesRows}
        disableSelectionOnClick
        columns={columns}
        pageSize={8}

      />
    </div>
  );
}
