
import "./performances.css";
import { userData } from "../../dummyData";
import Chart from "../../component/chart/Chart";
import LargeWidget from "../../component/largeWidget/LargeWidget";

export default function performances() {
  return (
    <div className="performances">
      <div className="performancesWidgets">
        <LargeWidget />
      </div>
      <Chart
        data={userData}
        title="CPU Utlization"
        grid
        dataKey="Server Performance"
      />
      <Chart
        data={userData}
        title="Disk Volume"
        grid
        dataKey="Server Performance"
      />
      <Chart
        data={userData}
        title="Database Availability"
        grid
        dataKey="Server Performance"
      />
    </div>
  );
}
