import React from "react";
import Chart from "../../component/chart/Chart";
import SquareInfo from "../../component/squareInfo/SquareInfo";
import "./home.css";
import { userData } from "../../dummyData.js";
import SmallWidget from "../../component/smallWidget/SmallWidget";
import LargeWidget from "../../component/largeWidget/LargeWidget";

export default function Home() {
  return (
    <div className="home">
      <SquareInfo />
      <Chart data={userData} title="Server Performance Analytics" grid dataKey="Server Performance"/>
      <div className="homeWidgets">
        <SmallWidget/>
        <LargeWidget/>
      </div>
    </div>
  );
}