import Topbar from "./component/topbar/Topbar";
import Sidebar from "./component/sidebar/Sidebar"
import "./App.css";
import Home from "./pages/home/Home";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import UserList from "./pages/userList/UserList";
import User from "./pages/user/User";
import NewUser from "./pages/newUser/NewUser";
import Server from "./pages/server/Server";
import NewServer from "./pages/newServer/NewServer";
import ServerList from "./pages/serverList/ServerList";
import Analytic from "./pages/analytic/Analytic";
import ApplicationList from "./pages/applicationUsage/ApplicationUsage";
import ProcessList from "./pages/process/Process";
import Performances from "./pages/performances/Performances";
import MessageList from "./pages/notification/Notification";

function App() {
  return (
    <Router>
      <Topbar/>
      <div className="container">
        <Sidebar/>
        <Routes>
         <Route exact path="/" element={<Home/>}/>
         <Route exact path="/users" element = {<UserList />}/>
         <Route exact path="/user/:userId" element={<User/>}/>
         <Route exact path="/newUser" element={<NewUser/>}/>
         <Route exact path="/notification" element={<MessageList />}/>
         <Route exact path="/analytics" element={<Analytic />}/>
         <Route exact path="/applicationUsage" element={<ApplicationList />}/>
         <Route exact path="/process" element={<ProcessList />}/>
         <Route exact path="/performances" element={<Performances />}/>
         <Route exact path="/server" element={ <ServerList />}/>
         <Route exact path="/server/:serverId" element={<Server />}/>
         <Route exact path="/newserver" element={ <NewServer />}/>
        </Routes>
      </div>
    </Router>
  );
}

export default App;

