
import "./analytic.css";
import { userData } from "../../dummyData.js";
import SmallWidget from "../../component/smallWidget/SmallWidget";
import Chart from "../../component/chart/Chart";

export default function Analytic() {
  return (
    <div className="analytic">
      <div className="analyticWidgets">
        <SmallWidget />
      </div>
      <Chart
        data={userData}
        title="Server Performance Analytics"
        grid
        dataKey="Server Performance"
      />
      <Chart
        data={userData}
        title="CPU Utlization"
        grid
        dataKey="Server Performance"
      />
      <Chart
        data={userData}
        title="Disk Volume"
        grid
        dataKey="Server Performance"
      />
      <Chart
        data={userData}
        title="Database Availability"
        grid
        dataKey="Server Performance"
      />
    </div>
  );
}