import "./sidebar.css";
import {
  LineStyle,
  Timeline,
  PermIdentity,
  BarChart,
  ChatBubbleOutline,
  Description,
  List,
  AccessTime,
} from "@material-ui/icons";
import { Link } from "react-router-dom";

export default function Sidebar() {
  return (
    <div className="sidebar">
      <div className="sidebarWrapper">
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">Dashboard</h3>
          <ul className="sidebarList">
            <Link to="/" className="link">
              <li className="sidebarListItem">
                <LineStyle className="sidebarIcon" />
                Home
              </li>
            </Link>
            <Link to="/analytics" className="link">
              <li className="sidebarListItem">
                <Timeline className="sidebarIcon" />
                Analytics
              </li>
            </Link>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">Reports</h3>
          <ul className="sidebarList">
            <Link to="/performances" className="link">
              <li className="sidebarListItem">
                <BarChart className="sidebarIcon" />
                Performances
              </li>
            </Link>
            <Link to="/process" className="link">
              <li className="sidebarListItem">
                <Description className="sidebarIcon" />
                Process
              </li>
            </Link>
            <Link to="/applicationUsage" className="link">
              <li className="sidebarListItem">
                <AccessTime className="sidebarIcon" />
                Application Usage
              </li>
            </Link>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">Manage Server</h3>
          <ul className="sidebarList">
            <Link to="/users" className="link">
              <li className="sidebarListItem">
                <PermIdentity className="sidebarIcon" />
                Users
              </li>
            </Link>
            <Link to="/server" className="link">
              <li className="sidebarListItem">
                <List className="sidebarIcon" />
                Add/ Remove Server
              </li>
            </Link>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">Notifications</h3>
          <ul className="sidebarList">
            <Link to="/notification" className="link">
              <li className="sidebarListItem">
                <ChatBubbleOutline className="sidebarIcon" />
                Alert Messages
              </li>
            </Link>
          </ul>
        </div>
      </div>
    </div>
  );
}
